.game{
    display: flex;
    margin: 15px auto;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

.grid{
    margin: 0px auto;
    background-color: black;
}

.cell{
    margin: 0;
    padding: 0;
    border: none;
}

.row-grid{
  height: 2.5rem;
}

img, .cell {
  width: 2.5rem;
  height: 2.5rem;
}

.cell-current{
  background-color: lightgreen;
}


.cell-wall{
  background-color: rgb(90, 90, 90);
}

.controls{
  position: fixed;
  bottom: 50px;
  left: 45%;
}

.button{
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid rgb(85, 85, 85);
  background-color: rgb(29, 29, 29);
  color: darkgoldenrod;
  margin: 1px;
  padding: 0;
  opacity: 0.7;
}

.btn-bomb img{
  width: 80%;
}


.button:hover{
  border-color: rgb(131, 131, 131);
}


@media only screen and (max-width: 940px) {
  .game{
    margin-top: 0px;
  }
  .cell, img{
    
    width: 2.2rem;
    height: 2.2rem;
  }
  .row-grid{
    height: 2.2rem;
  }
  .controls{
    left: 40%;
  }
}

@media only screen and (max-width: 800px) {
  .cell, img{
    width: 2.0rem;
    height: 2.0rem;
  }
  .row-grid{
    height: 2.0rem;
  }
}

@media only screen and (max-width: 710px) {
  .game{
    margin-top: 0px;
  }
  .cell, img{
    
    width: 1.8rem;
    height: 1.8rem;
  }
  .row-grid{
    height: 1.8rem;
  }
}

@media only screen and (max-width: 636px) {
  .game{
    margin-top: 0px;
  }
  .cell, img{
    
    width: 1.6rem;
    height: 1.6rem;
  }
  .row-grid{
    height: 1.6rem;
  }
  .controls{
    left: 35%;
  }
}